@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
body {
  margin: 0;
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

:root {
  --bg-colour-1: #536976;
  --bg-colour-2: #292e49;
}
.body {
    margin: 0 0 0 140px;
    padding: 20px;
    height: 100%;
}
.nav-outer {
  position: absolute;
}

.nav-inner {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 4px solid gray;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-options div {
  background-color: white;
  padding: 0.4rem 0.8rem;
  border-radius: 999px;
  text-align: center;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.45);
}

.nav-ball {
  --x-position: 0;
  --y-position: 0;
  position: absolute;
  /* half of the hight of the outer circle subtract half the height of the circle */
  margin-top: calc(100px - 20px);
  z-index: 99;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  touch-action: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transform: translate(var(--x-position), var(--y-position));
}

.nav-ball div {
  border-radius: 50%;
  height: 100%;
  width: 100%;
  background-color: rgb(165, 158, 158);
  pointer-events: none;
}

.nav-ball.selected div {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%,
  100% {
    background-color: rgb(165, 158, 158);
  }
  50% {
    background-color: rgba(165, 158, 158, 0.5);
  }
}

.nav-ball.selected {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.45);
}


.area {
  perspective: 600px;
}

.cube {
  position: relative;
  transform-style: preserve-3d;
  transform: rotateX(var(--rotate-y)) rotateY(var(--rotate-x))
    rotateZ(var(--rotate-z)) translateZ(calc(var(--rectangle-height) / -2));

  width: var(--rectangle-width);
  height: var(--rectangle-height);
  transition: all 0.1s;
}

.orbit {
  width: 600px;
  height: 600px;
  border-radius: 75%;
  border: 4px solid rgba(176, 175, 207, 0.548);
  position: absolute;
}

