.cube-face-front {
  position: absolute;
  width: var(--rectangle-width);
  height: var(--rectangle-height);
  border: 1px solid rgba(176, 175, 207, 0.548);
}

.cube-face-side {
  position: absolute;
  width: var(--rectangle-height);
  height: var(--rectangle-height);
  border: 1px solid rgba(176, 175, 207, 0.548);
}

.cube-face--front {
  transform: rotateY(0deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(128, 128, 128, 0.432);
  display: flex;
  justify-content: center;
  align-items: center;
}

.cube-face--back {
  transform: rotateY(180deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(0, 0, 0, 0);
}

.cube-face--right {
  transform: rotateY(90deg) translateZ(calc(0.75 * var(--rectangle-width)));
  background: rgba(0, 0, 0, 0);
}

.cube-face--left {
  transform: rotateY(270deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(0, 0, 0, 0, 0);
}

.cube-face--top {
  transform: rotateX(90deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(0, 0, 0, 0, 0);
}

.cube-face--bottom {
  transform: rotateX(-90deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(0, 0, 0, 0, 0);
}
.about-page {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  --rectangle-width: 300px;
  --rectangle-height: 150px;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3 {
  margin: 0;
}

p {
  margin: 0.5em 0;
}

.content {
  margin: 0 auto;
  padding: 10px 15px;
  background-color: white;
  font-size: 12px;
  box-shadow: 0px 0px 7.5px 0px rgba(0, 0, 0, 0.75);
}

.header {
  margin: 0 0 15px 0;
}

.experience-node {
  display: flex;
  align-items: center;
}

.header-socials {
  background-color: lightgray;
  margin: 10px -15px;
  padding: 10px 15px;
  display: flex;
  justify-content: space-evenly;
  overflow-wrap: anywhere;
}

.header-socials > p {
  display: inline-block;
  text-align: center;
}

.label {
  font-weight: 800;
}

.date {
  font-style: italic;
  font-weight: 400;
  color: gray;
}

.circle-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-self: stretch;
  align-items: center;
  margin: 0 10px 0 0;
}

.circle {
  width: 30px;
  height: 30px;
  border-radius: 60%;
  border: 2px solid gray;
}

.circle-container::before {
  content: "";
  border: 1px solid gray;
  height: calc(100% / 2 - 30px);
}

.circle-container::after {
  content: "";
  border: 1px solid gray;
  height: calc(100% / 2 - 30px);
}

.resume-body {
  -webkit-column-count: 2;
          column-count: 2;
  grid-column-gap: 4em;
  -webkit-column-gap: 4em;
          column-gap: 4em;
}

.heading {
  width: 100%;
  border-bottom: 3px solid blue;
}

.heading > h2 {
  color: blue;
}

@media print {
  @page {
    size: 21cm 29.7cm;
  }
  .content {
    -webkit-print-color-adjust: exact;
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    margin: 0 auto;
    box-shadow: none;
    /* padding: 10px 15px; */
  }
}

.page {
  height: 100vh;
  background: red;
  background: linear-gradient(
    to right,
    #536976,
    #292e49);
  background: linear-gradient(
    to right,
    var(--bg-colour-1, #536976),
    var(--bg-colour-2, #292e49)
  );
  --rotate-x: 0deg;
  --rotate-y: 0deg;
  --rotate-z: 0deg;
}

